.header {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  background-color: @gray;
  position: relative;
  padding-top: 21px;
  margin-bottom: -73px;
  z-index: 1;

  .container {
    display: contents;

    @media (min-width: 1200px) {
      height: 50px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 32px;
    }
  }

  @media (min-width: 660px) {
    padding-top: 27px;
    margin-bottom: -110px;
  }

  @media (min-width: 1200px) {
    flex-direction: row;
    justify-content: space-between;
    padding-right: 130px;
    padding-left: 130px;
    background-color: fade(@black, 30%);
  }
}

.header__wrapper {
  width: 280px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 21px;
  padding-bottom: 5px;

  @media (min-width: 660px) {
    width: 620px;
    margin-bottom: 42px;
    padding-bottom: 0;
  }

  @media (min-width: 1200px) {
    display: contents;
  }
}

.nav {
  position: relative;
  flex-basis: 100%;

  @media (min-width: 1200px) {
    flex-basis: auto;
  }
}

.header__button {
  border: none;
  padding: 0;
  background-color: transparent;

  @media (min-width: 1200px) {
    display: none;
  }
}

.header__button--cross {
  width: 23px;
  height: 23px;
  position: relative;

  &:hover {
    &::before,
    &::after {
      background-color: @pink;
    }
  }

  &:active {
    &::before,
    &::after {
      background-color: fade(@pink, 30%);
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 29px;
    height: 3px;
    background-color: @white;
    right: -3px;
    top: 10px;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.header__button--burger {
  width: 50px;
  height: 24px;
  position: relative;

  &:hover {
    &::before {
      background-color: @pink;
      box-shadow: 0 10px 0 0 @pink, 0 20px 0 0 @pink;
    }
  }

  &:active {
    &::before {
      background-color: fade(@pink, 30%);
      box-shadow: 0 10px 0 0 fade(@pink, 30%), 0 20px 0 0 fade(@pink, 30%);
    }
  }

  &::before {
    content: "";
    position: absolute;
    width: 50px;
    height: 4px;
    background-color: @white;
    border-radius: 1px;
    box-shadow: 0 10px 0 0 @white, 0 20px 0 0 @white;
    left: 0;
    top: 0;
  }
}

.nav__list {
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;

  @media (min-width: 1200px) {
    width: 620px;
    flex-direction: row;
    justify-content: space-between;
  }
}

.nav__item {
  list-style: none;
  text-align: center;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.15), 0 -1px 0 @black;

  @media (min-width: 1200px) {
    box-shadow: none;
  }
}

.nav__link {
  display: block;
  text-transform: uppercase;
  color: @white;
  font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  padding: 16px 0;

  &:hover {
    color: @pink;
  }

  &:active {
    color: fade(@white, 30%);
  }

  @media (min-width: 1200px) {
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    text-transform: none;
    padding: 0;

    &--current {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: @white;
        bottom: -13px;
      }
    }
  }
}

.header__logo {
  position: relative;
  display: block;
  width: 74px;
  height: 23px;

  &:hover path {
    fill: @pink;
  }

  &:active path {
    fill: @white;
    opacity: 0.3;
  }

  @media (min-width: 660px) {
    width: 145px;
    height: 40px;
  }
}
